@charset "UTF-8";
@import url(../../dist/css/normalize.css);
@font-face {
  font-family: "Sora-Regular";
  src: url("../assets/fonts/Sora-Regular.ttf");
}
@font-face {
  font-family: "Sora-Bold";
  src: url("../assets/fonts/Sora-Bold.ttf");
}
@font-face {
  font-family: "Sora-ExtraBold";
  src: url("../assets/fonts/Sora-ExtraBold.ttf");
}
@font-face {
  font-family: "Sora-SemiBold";
  src: url("../assets/fonts/Sora-SemiBold.ttf");
}
.h1 {
  color: #71717A;
  font-family: "Sora-Regular", sans-serif;
  font-size: 20px;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

.block_flip-card {
  display: flex;
  justify-content: center;
}

.flip-card {
  perspective: 1000px;
  width: 186px;
  height: 186px;
  margin-left: 30px;
}
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
  cursor: pointer;
}
.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.flip-card-back {
  transform: rotateY(180deg);
}
.flip-card img {
  max-width: 100%;
  max-height: 100%;
}

.logo {
  perspective: 1000px;
  width: 140px;
  height: 20px;
}
.logo-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  animation: flip 10s infinite; /* Автоматическая анимация (3 секунды) */
}
.logo-front, .logo-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo-back {
  transform: rotateY(180deg);
}
.logo .logo-img {
  max-width: 100%;
  max-height: 100%;
}

@keyframes flip {
  0% {
    transform: rotateY(0deg);
  } /* Начало (лицевая сторона) */
  10% {
    transform: rotateY(180deg);
  } /* Быстрый переворот (10% от 3 сек = 0.3 сек) */
  50% {
    transform: rotateY(180deg);
  } /* Обратная сторона видна до 50% (1.5 сек) */
  60% {
    transform: rotateY(360deg);
  } /* Быстрый возврат (ещё 0.3 сек) */
  100% {
    transform: rotateY(360deg);
  } /* Лицевая сторона видна до конца (3 сек) */
}